.app__video {
    height: 100vh;
    position: relative;
}

.app__video video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.app__video-overlay {
    position: absolute;
    inset: 0;
    background: rgba(0,0,0,0.6);
}

.app__video-overlay_circle {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 2px solid var(--color-golden);
    cursor: pointer;

}

@media screen and (max-width:1000px) {
    .app__video-overlay_circle {
        width: 100px;
        height: 100px;
    }
}