.app__chef-content {
    display: flex;
    flex-direction: column;
    width: 100%;

    margin-top: 5rem;
}

.app__chef-content_quote {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end
}

.app__chef-content_quote img {
    width: 47px;
    height: 40px;
    margin: 0 1rem 1rem 0;
}

.app__chef-sign {
    width: 100%;
    margin-top: 3rem ;

}

.app__chef-sign p:first-child {
    font-weight: 400;
    font-size: 30px;
    line-height: 41.6px;
    color: var(--color-golden);

    margin-bottom: 0.4rem;
}

.app__chef-sign  img {
    width: 250px;
    margin-top: 3rem;
}

@media screen and (max-width: 2000px) {
    .app__chef-sign img {
        width: 370px;
    }
    
}

@media screen and (max-width: 500px) {
    .app__chef-content {
        margin-top: 2rem
    }
    .app__chef-sign img {
        width:80%;
    }
    
}